.fe-picklist-background {
    width: 196px;
    max-height: 150px;
    overflow-y: auto;
    line-height: 26px;
    margin-left: 351px;
    position: absolute;
    background: #fff;
    padding: 10px;
    border: 1px solid #d8dde8;
    border-radius: 3px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.15);
    z-index: 20;
}

.fe-picklist-select input {
    margin-right: 6px;
}

.fe-blocker {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 0;
}