.fe-save-area {
    border-top: 2px solid #d8dde6;
    padding: 20px;
    position: fixed;
    width: calc(100% - 40px);
    bottom: 0;
    font-size: 14px;
    background: #fff;
}

.fe-save-button {
    float: right;
    padding: 8px 14px;
    background: #0071d2;
    color: #fff;
    border-radius: 4px;
    cursor: pointer;
}

.fe-save-area label {
    margin-left: 5px;
}

.fe-checkbox-save {
    float: left;
    margin-top: 6px;
}

.fe-save-as-button {
    float: right;
    padding: 7px 13px;
    border: 1px solid #0071d2;
    color: #0070d2;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 10px;
    background: #fff;
}